.cards_container{
  display: flex;
  flex-wrap: wrap;
  gap:10px;
}

.card{
  display: flex;
  flex-direction: column;
  gap: 3px;
  height: 10rem;
  width: 10rem;
  align-items: center;
  border: 5px rgb(15, 143, 143) solid;
  border-radius: 5px;
  background-color: rgb(75, 162, 162);
  cursor: pointer;
  transition: box-shadow ease-in 0.4s;
}

.card:hover{
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}

.card img{
  height: 7rem;
  width: 7rem;
}

.card label{
  font-weight: 700;
  flex:1;
}